.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-primary{
  background-color: #00be6e !important;
}

.contact-button {
  cursor: pointer;
  height: 108px;
  display: block;
  width: 108px;
  font-size: 36px;
  color: #33475b;
  border-radius: 50%;
  background: white;
  border: 1px solid #f8f9fa;
  box-shadow: 0px 6px 20px -10px #adb5bd;
}

.contact-button:hover{
  box-shadow: 0px 0px 30px -3px #adb5bd;
}

.bg-gray-1{
  background-color: #33475b;
}
.card-shadow{
  box-shadow: 0px 0 30px -5px #adb5bd;
}

.header-align {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  padding: .375rem .75rem;
}

.navbar .navbar-brand{
  font-size: 24px;
}

.node{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: white;
}

.node.selected{
  border: 3px solid #33475b;
}

.loader {
  border: 5px solid #f3f3f3;
  border-top: 5px solid #00be6e;
  border-radius: 50%;
  width: 96px;
  height: 96px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

div.modal-dialog.side-modal{
  min-height: 100vh;
  margin-left: auto;
  border-radius: 0px;
  margin-top: 0px;
  margin-right: 0px;
}
div.modal-dialog.side-modal > .modal-content{
  border-radius: 0px !important;
    min-height: 100vh;
}

div.modal-dialog.side-modal .modal-header{
  border-radius: 0px !important;
}

div.modal-dialog.side-modal .modal-header .close{
  color: white;
}


.card-rounded{
  border-radius: 12px;
}

.card-left-rounded{
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
}
.side-pane-top {
  display: flex;
  width: 100%;
  height:50%;
  margin-bottom: 0px;
  padding: 14px 10px;
  font-weight: bold;
  color: grey;
  border-top-right-radius: 12px;
}
.side-pane-bottom-button{
  width:100%;
  height:50%;
  display:block;
  border: 0px;
  font-weight:bold;
  text-align: right;
  border-bottom-right-radius: 12px;
}

.count{
  font-size:36px;
}

.bg-success-light{
  background-color: #28a74538!important;
}

.associate-card-addon{
  padding-top: 35px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.contact-card{
  min-height: 100px;
}

.card-selected{
  border: 2px solid green;
  border-left: 20px solid green;
}

.card-not-selected{
  border: 2px solid grey;
  border-left: 20px solid grey;
}

.contact-list-button{
  height: 80px;
    border-radius: 50%;
    width: 80px;
    box-shadow: 0px 0px 0px 15px #bae8c5;
}
